@import url(https://fonts.googleapis.com/css?family=Saira+Stencil+One);

.home-page {
  position: relative;
}

.home-content {
  display: flex;
  justify-content: between;
  align-items: center;
  height: 100vh;
  width: 100%;
  margin: auto;
  padding: 0 4rem;
}

.home-left {
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: left;
  padding-left: 4rem;
  font-size: 60px;
}

.home-right {
  width: 45%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  text-align: left;
}

@media only screen and (max-width: 600px) {
  /* Styles */
  .home-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 4rem;
  }
  .home-left, .home-right {
    width: 100%;
    height: 50%;
    font-size: 40px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    text-align: center;
  }
  .button-85 {
    width: 8rem;
  }
  .console-box {
    font-size: 0.8rem;
    width: 21rem!important;
    height: 12rem!important;
    padding: 0.8rem 0 0.8rem 0;
  }
}

.home-page-bg {
  position: absolute;
  inset: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: 90%;
  filter: brightness(0.7) blur(2px);
  z-index: -1;
}

/* CSS */
.button-85 {
  padding: 0.4em 2em;
  margin-top: 1.5em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #1D283A;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 16rem;
  height: 4rem;
  font-size: 1.5rem;
}

