.profile-text {
  color: #FBBF24;
  z-index: 0;
  display: inline;
  font-weight: 400;
  border-right: 4px solid #FBBF24;
  white-space: nowrap;
  animation: blinkTextCursor 500ms infinite;
  padding-top: -2rem;
}

@keyframes blinkTextCursor {
  from {
    border-right-color: #FBBF24;
  }
  to {
    border-right-color: transparent;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.console-box {
  height: 13rem;
  width: 28rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-right: 10px;
  background-color: #1d283a;
  border-radius: 0.75rem;
  padding: 1.5rem;
  text-align: left;
  box-sizing: border-box;
  border-width: 0;
  border-style: solid;
  border-color: #e5e7eb;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    Liberation Mono, Courier New, monospace;
}

.console-head {
  display: flex;
  column-gap: 0.6rem;
}

.console-body {
  margin-top: 10px;
}

.console-head-dot {
  height: 1rem;
  width: 1rem;
  background-color: #78797a;
  border-radius: 50%;
}

.console-body-row {
  display: flex;
  margin: 0.5em 0;
  line-height: 1;
  font-size: 1.3em;
  text-align: center;
  vertical-align: center;
}

.console-body-text {
  margin-left: 1.2rem;
}

.overlay {
  position: fixed;
  inset: 0;
  background: white;
  transition: 500ms opacity;
  visibility: hidden;
  opacity: 0;
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

.pop-up {
  background: #fff;
  width: 60%;
  height: 100vh;
  position: relative;
  transition: all 5s ease-in-out;
  border-radius: 5px; 
}